import * as React from "react";

import PageContainer from "Pages/styled";
import ProgressIndicator from "./styled";

import { HiddenH1 } from "components";

const ConnectingPage = () => {
    return (
        <>
            <HiddenH1 label="Connecting..." />
            <PageContainer maxWidth={false} disableGutters={true}>
                <ProgressIndicator thickness={5} size={100} />
            </PageContainer>
        </>
    );
};

export default React.memo(ConnectingPage);
