type Node = string | number | boolean;

interface Props {
    node: Node;
    preProcess?: (arg0: Node) => string;
    label?: string;
}

const Paragraph = (props: Props) => {
    const { node, preProcess, label } = props;
    return (
        <p className="details__item">
            <strong className="details__strong">{label + ": "}</strong>
            {preProcess ? preProcess(node) : node}
        </p>
    );
};

export default Paragraph;
